.edit_product {
  height: 300px;
  width: 30%;
  background-color: rgba(255, 255, 255, 0.13);
  margin: auto;
  position: absolute;
  top: 20px;
  left: 25%;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
